.nav-container {
  display: flex;
  flex-direction: column;
  position: relative;
  padding: 0 0.5rem;
}
.nav-center {
  display: flex;
  align-items: center;
  justify-content: center;
}
.nav-center h2 {
  text-transform: uppercase;
  color: #333;
  font-weight: bold;
  letter-spacing: 0.25rem;
}
.cafe-logo {
  object-fit: cover;
}
.item-details {
  display: none;
}

.show-item-details {
  display: block;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.show-item-details button {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  background-color: transparent;
  color: #000;
  font-weight: bold;
  border: 2px solid rgba(0, 0, 0, 0.5);
  border-radius: 0.25rem;
  transition: all 0.3 linear;
}
.show-item-details button:hover {
  background-color: #000;
  color: #fff;
}
@media screen and (max-width: 450px) {
  .cafe-logo {
    display: none;
  }
}
@media screen and (max-width: 600px) {
  .nav-center {
    flex: 0 0 80px;
  }
  .cafe-logo {
    width: 80px;
  }
  .nav-center h2 {
    font-size: 2rem;
  }

  .show-item-details {
    right: 10px;
    width: 50px;
    height: 50px;
    padding: 2px;
  }
  .show-item-details button {
    padding: 4px;
  }
}
@media screen and (min-width: 601px) and (max-width: 900px) {
  .nav-center {
    flex: 0 0 85px;
  }
  .cafe-logo {
    width: 85px;
  }
  .nav-center h2 {
    font-size: 2.2rem;
  }
  .show-item-details {
    right: 12px;
    width: 55px;
    height: 55px;
    padding: 3px;
  }
  .show-item-details button {
    padding: 4px;
    font-size: 0.9rem;
  }
}
@media screen and (min-width: 901px) {
  .nav-center {
    flex: 0 0 90px;
  }
  .cafe-logo {
    width: 90px;
  }
  .nav-center h2 {
    font-size: 2.4rem;
  }
  .show-item-details {
    right: 14px;
    width: 60px;
    height: 60px;
    padding: 4px;
  }
  .show-item-details button {
    padding: 6px;
    font-size: 1rem;
  }
}
