.item-article {
  width: 100%;
  height: 100%;
  border: 1px solid rgba(0, 0, 0, 0.3);
  background-color: #fff;
  border-radius: 0.25rem;
  transition: all 0.3s linear;
  cursor: pointer;
  /* border: 1px solid red; */
}

.item-article:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}
.item-container {
  height: 100%;
}
.item-center {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.item-center img {
  width: 100%;
  object-fit: fill;
  border-radius: 5px;
  /**/
  /* margin-bottom: 10px; */
  /* height: 155px; */
}
.item-info {
  display: flex;
  justify-content: space-between;
  background-color: rgba(255, 255, 255, 0.8);
  /* border: 1px solid red; */
  /**/
  padding: 2px;
  margin-bottom: 2px;
}
.item-info p {
  /* font-weight: bold; */
  /* border: 1px solid rebeccapurple; */
}
.item-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  /* display: block; */
}
.itemAdd-btn {
  flex: 1 0 auto;
  /* width: 100%; */
  /* margin-top: 5px; */
  letter-spacing: 3px;
  border-color: transparent;
  background-color: #007bff;
  color: #fff;
  /* padding: 6px; */
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s linear;
}
.itemAdd-btn:hover {
  background-color: #0056b3;
  color: #fff;
  border-radius: 0.25rem;
}
@media screen and (max-width: 600px) {
  .item-article {
    padding: 3px;
  }
  .item-center img {
    height: 155px;
    margin-bottom: 2px;
  }
  .item-info {
    flex-direction: column;
    align-items: center;
    /* padding: 2px; */
    /* margin-bottom: 2px; */
    /* border: 1px solid blue; */
  }

  .item-info p {
    margin-bottom: 2px;
  }

  .itemAdd-btn {
    margin-top: 2px;
    padding: 3px;
  }
}

@media screen and (min-width: 601px) and (max-width: 900px) {
  .item-article {
    padding: 4px;
    /* border: 1px solid blue; */
  }
  .item-center img {
    height: 170px;
    margin-bottom: 3px;
  }

  .item-info p {
    /* margin: 0 3px; */
    /* border: 1px solid red; */
  }

  .itemAdd-btn {
    margin-top: 4px;
    padding: 5px;
  }
}
@media screen and (min-width: 901px) {
  .item-article {
    padding: 5px;
    /* border: 1px solid red; */
  }
  .item-center img {
    height: 190px;
    margin-bottom: 4px;
  }

  .item-info p {
    /* margin: 0 4px; */
    /* border: 1px solid red; */
  }
  .itemAdd-btn {
    margin-top: 5px;
    padding: 6px;
  }
}
